.register-title {
    font-family: "Proxima Nova", sans-serif;
    font-weight: bold;
    font-size: 25px;
    color: white;
    text-align: center;
}

.input-container {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.input {
    background-color: rgba(38, 46, 70, 0);
    height: 50px;
    width: 100%;
    border: 2px solid #737373;
    border-radius: 10px;
    color: white;
    font-family: "Proxima Nova", sans-serif;
    font-weight: 100;
    font-size: 15px;
    padding: 0 20px;
}

.input::placeholder {
    color: #737373;
    font-size: 15px;
}

.select-input {
    background-color: rgba(38, 46, 70, 1);
    height: 50px;
    width: 100%;
    border: 2px solid #737373;
    border-radius: 10px;
    color: white;
    font-family: "Proxima Nova", sans-serif;
    font-weight: 100;
    font-size: 15px;
    padding: 0 20px;
}

.error {
    color: #ff0000;
    font-family: "Proxima Nova", sans-serif;
    font-weight: 100;
    font-size: 14px;
    margin-top: -10px;
    margin-left: 5px;
}

.preloader {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

