.header-title {
    color: #fff;
    /*font-size: 64px;*/
    font-family: "Proxima Nova", sans-serif;
    font-weight: 700;
    text-align: center;
}

.header-description{
    color: #ffffff;
    font-size: 20px;
    font-family: "Proxima Nova", sans-serif;
    font-weight: 400;
    text-align: center;
}

.hero-section{
    background-image: linear-gradient(45deg, rgba(15, 21, 39, 0.8), rgba(15, 21, 39, 0.8)), url("../../assets/images/background.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.header-button{
    border: 2px solid #c2ae64;
    border-radius: 25px;
    color: #ffffff;
    font-family: "Proxima Nova", sans-serif;
    font-weight: bold;
    font-size: 20px;
    width: 180px;
    height: 60px;
    margin-top: 20px;
}

.header-button:hover{
    background-color: #c2ae64;
    color: #ffffff;
}
