.navbar {
    background-color: #040A1C;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 40px 100px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    position: fixed;
    width: 100%;
}

.navbar-mobile {
    background-color: #040A1C;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    position: fixed;
    width: 100%;
}

.nav-menu-mobile{
    height: 35px;
}
.nav-logo {
    height: 60px;
    width: 195px;
}

.nav-logo-mobile {
    height: 50px;
}

.navItems {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    font-family: "Proxima Nova", sans-serif;
    font-weight: 500;
    font-size: 18px;
    color: white;
}

.navItems-mobile{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    font-family: "Proxima Nova", sans-serif;
    font-weight: 500;
    font-size: 18px;
    color: white;
}

.active {
    text-decoration: underline #c2ae64 0.15rem;
    text-underline-offset: 0.5rem;
}

.navButton{
    background-color: #c29b03;
    border: none;
    border-radius: 30px;
    font-family: "Proxima Nova", sans-serif;
    font-weight: bold;
    font-size: 20px;
    color: #ffffff;
    height: 45px;
    width: 150px;
}
.navButton:hover{
    background-color: #8d7500;
}
